E,kBAAA,CAGF,UACE,gBAAA,CAGF,WACE,oBAAA,CAAA,CAIJ,sCACE,kBACE,oCAAA,CAGF,4BACE,UAAA,CACA,WAAA,CAAA,CAIJ,sCACE,MACE,gBAAA,CAGF,mBACE,YAAA,CAGF,iBACE,cAAA,CAGF,uBACE,cAAA,CAGF,KACE,gBAAA,CAGF,QACE,gBAAA,CAGF,kBACE,oCAAA,CAAA","sourcesContent":["@import url(\"https://fonts.googleapis.com/css2?family=Poppins&display=swap\");\n\n$text-primary: #000000;\n$text-secondary: #ffffff;\n\n$background-yellow: #f6c90e;\n\n$logo-color-primary: #ffd62d;\n$logo-color-secondary: #d62805;\n\n$logo-gray-primary: #cccccc;\n$logo-gray-secondary: #696969;\n\n* {\n  padding: 0;\n  margin: 0;\n  box-sizing: border-box;\n}\n\nbody,\nhtml {\n  margin: 0;\n  padding: 0;\n  width: 100%;\n  height: 100%;\n}\n\nbody {\n  font-family: \"Poppins\", sans-serif;\n  font-size: 1rem;\n}\n\n/*\n * Skip to Content\n */\n\n.skip-link {\n  position: absolute;\n  top: -41px;\n  left: 0;\n  background-color: #bf1722;\n  color: white;\n  padding: 8px;\n  z-index: 100;\n}\n\n.skip-link:focus {\n  top: 0;\n}\n\n/*\n * Header\n *\n */\n\n.header {\n  min-height: 60px;\n  transition: min-height 0.3s;\n  display: flex;\n  align-items: center;\n}\n\n.header__inner {\n  width: 260px;\n  margin-right: auto;\n}\n\n.header__title {\n  font-weight: 300;\n  font-size: 4em;\n  margin: 0.25em 0;\n  display: inline-block;\n}\n\n/*\n* Drawer\n*\n*/\n\n#drawer {\n  width: 250px;\n  height: 100%;\n  position: absolute;\n\n  /* menyembunyikan drawer -250px (sesuai dengan ukuran lebar drawer) ke kiri */\n  transform: translate(-250px, 0);\n  transition: transform 0.3s ease-in-out;\n  background: white;\n  top: 0;\n  z-index: 1;\n}\n\n#drawer.open {\n  transform: translate(0, 0);\n}\n\n#drawer li {\n  border-bottom: 1px black solid;\n}\n\n#drawer a {\n  text-decoration: none;\n  font-size: 1rem;\n  color: black;\n  min-width: 44px;\n  min-height: 44px;\n  padding: 16px 0;\n}\n\n#hamburger {\n  font-size: 32px;\n  font-weight: bold;\n  min-width: 44px;\n  min-height: 44px;\n  text-decoration: none;\n  color: black;\n  text-align: center;\n  margin-right: 0.5rem;\n  background-color: transparent;\n  border: none;\n  cursor: pointer;\n}\n\n/*\n * Top level navigation\n */\n\n.nav {\n  width: 100%;\n  margin-left: auto;\n  margin-right: auto;\n}\n\n.nav__list {\n  width: 100%;\n  padding: 0;\n  margin: 0;\n  text-align: right;\n}\n\n.nav__item {\n  box-sizing: border-box;\n  display: block;\n  width: auto;\n  text-align: center;\n  line-height: 24px;\n  padding: 24px;\n  text-transform: uppercase;\n}\n\n.nav a {\n  text-decoration: none;\n  color: $text-primary;\n  min-width: 44px;\n  min-height: 44px;\n}\n\n/*\n * Jumbotron\n */\n\n.hero {\n  display: flex;\n  align-items: center;\n  min-height: 380px;\n  width: 100%;\n  text-align: center;\n  background-image: url(\"../public/images/heros/hero-image_2.jpg\");\n  background-position: center;\n  background-size: cover;\n  background-color: green;\n}\n\n.hero__inner {\n  margin: 0 auto;\n  max-width: 800px;\n  background-color: $logo-color-secondary;\n  opacity: 0.8;\n  padding: 0.5rem;\n}\n\n.hero__title {\n  color: $text-secondary;\n  font-weight: 500;\n  font-size: 2rem;\n}\n\n.hero__tagline {\n  color: $text-secondary;\n  margin-top: 16px;\n  font-size: 1.2rem;\n  font-weight: 300;\n}\n\n/*\n * main\n */\n\nmain {\n  width: 100%;\n  margin: 0 auto;\n}\n\n.restaurant {\n  width: 100%;\n  margin: 60px auto;\n  text-align: center;\n}\n\n.restaurant__label {\n  font-size: 32px;\n  font-weight: lighter;\n}\n\n.restaurant__list {\n  margin: 32px auto auto;\n  padding: 16px;\n  text-align: left;\n  overflow: auto;\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: 0px 10px;\n}\n\n.restaurant-item {\n  margin-top: 16px;\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n  width: 100%;\n  border-radius: 5px;\n  overflow: hidden;\n}\n\n.restaurant-item:hover {\n  box-shadow: 0 4px 8px 0 $background-yellow;\n  transition: 0.8ms;\n}\n\n.restaurant-item__content {\n  padding: 16px 16px 32px 16px;\n}\n\n.restaurant-item__thumbnail {\n  width: 100%;\n  height: 50vw;\n}\n\n.restaurant-item__title {\n  font-weight: 500;\n  font-size: 18px;\n  margin: 16px 0px;\n  transition: 0.3s opacity;\n}\n\n.restaurant-item__title:hover {\n  opacity: 0.5;\n}\n\n.restaurant-item__title a {\n  text-decoration: none;\n  color: inherit;\n  min-width: 44px;\n  min-height: 44px;\n  padding: 12px 0;\n}\n\n.restaurant-item__subtitle {\n  font-size: 1rem;\n  text-transform: uppercase;\n  color: $logo-gray-secondary;\n}\n\n.restaurant-item__subtitle__info {\n  display: inline-block;\n  text-decoration: none;\n  color: $logo-color-secondary;\n  padding: 12px 8px 12px 8px;\n  border: 1px solid $background-yellow;\n  border-radius: 8px;\n}\n\n.restaurant-item__description {\n  margin-top: 16px;\n  font-size: 14px;\n  line-height: 1.5em;\n}\n\n/*\n * footer\n */\n\nfooter {\n  background-color: $text-primary;\n  padding: 2em;\n  width: 100%;\n  text-align: center;\n}\n\nfooter ul {\n  margin: 0 auto;\n  display: inline-block;\n}\n\nfooter li {\n  display: inline-block;\n  margin: 0 1em;\n  font-size: 14px;\n}\n\nfooter a {\n  text-decoration: none;\n  color: $text-secondary;\n  min-width: 44px;\n  min-height: 44px;\n}\n\n/* button */\nbutton {\n  box-sizing: border-box;\n  min-width: 44px;\n  min-height: 44px;\n}\n\n@media screen and (min-width: 670px) {\n  .headline__title {\n    font-size: 18px;\n  }\n\n  .restaurant-item__content {\n    padding: 16px;\n  }\n\n  #hamburger {\n    display: none;\n  }\n\n  #drawer {\n    position: relative;\n    width: 100%;\n    height: auto;\n    transform: translate(0, 0);\n  }\n\n  #drawer li {\n    border-bottom: none;\n  }\n\n  #drawer a {\n    font-size: 1.3rem;\n  }\n\n  .nav__item {\n    display: inline-block;\n  }\n}\n\n@media screen and (min-width: 1000px) {\n  .restaurant__list {\n    grid-template-columns: repeat(2, 1fr);\n  }\n\n  .restaurant-item__thumbnail {\n    width: 100%;\n    height: 20vw;\n  }\n}\n\n@media screen and (min-width: 1200px) {\n  .hero {\n    min-width: 1000px;\n  }\n\n  .headline__content {\n    padding: 32px;\n  }\n\n  .headline__title {\n    font-size: 24px;\n  }\n\n  .headline__description {\n    font-size: 18px;\n  }\n\n  main {\n    max-width: 1200px;\n  }\n\n  .latest {\n    max-width: 1200px;\n  }\n\n  .restaurant__list {\n    grid-template-columns: repeat(3, 1fr);\n  }\n}\n"],"sourceRoot":""}]);
